import React from 'react'
import styled from 'styled-components'

import { Link, useParams } from 'react-router-dom'
import { useTo } from '../../util'

function useLinkTo(path, exact=false) {
  const { policy_id, attr_id } = useParams()

  const eid = exact ? attr_id : ':attr_id(\\d+)'
  return useTo(`checklist/params/${eid}`, exact) + `/${path}`
}


export default function(props) {

  const { parameterId } = props
  return (
    <Widget>
      <div className='title'> Elements </div>
      <List>
        <Icon to={useLinkTo('faq', true)}> <Faq /> </Icon>
        <Icon to={useLinkTo('eg', true)}> <Eg /> </Icon>
        <Icon to={useLinkTo('opn', true)}> <Operating /> </Icon>
        <Icon to={useLinkTo('sops', true)}> <Sop /> </Icon>
        { parameterId ? <Icon to={useLinkTo('steps', true)}> <Steps /> </Icon> : null }
      </List>


    </Widget>
  )
}


